import React, {useState, useEffect, useRef, useContext, Fragment} from 'react';

import MensajeContexto from '../Mensaje/MensajeContexto';

import Lottie from 'lottie-react';
import animationData from '../../data/Seccion_2/data.json';

import "./Seccion_2.css"

const Seccion_2 = (props) => {
    const [altura, setAltura] = useState(0)
    const [ancho, setAncho]   = useState(0)
  
    useEffect(() => {
        setAltura( window.innerHeight )
        setAncho( window.innerWidth )
    }, [altura, ancho])

    const refSeccion2 = useRef(null);

    const [animacion, setAnimacion] = useState(false);

    const { mostrarMensaje, updateMostrarMensaje } = useContext(MensajeContexto);

    const scrollCallback = (entries) => {
        if (entries[0].isIntersecting) {
            updateMostrarMensaje(false);
            setAnimacion(true);
        }else{
            // do nothing
        }
    };

    useEffect(() => {
        // *** Grab the element related to this callback
        const { current } = refSeccion2;
        const observer = new IntersectionObserver(scrollCallback, {
            root: null,
            threshold: 1,
        });
        observer.observe(current);
        return () => {
            observer.disconnect(current); // *** Use the same element
        }
    }, [refSeccion2.current]); // *** Note dependency

    const styleBox = {
        top: 0,
        padding: 0,
        border: 0,
        "background-color": "transparent",
        width: "100px",
        height: "100px",
        position: "relative",
        margin: "auto",
        transform: "translate(0%, 400%)",
        zIndex: "-1000"
    };

    let style = {
        top: 0,
        margin: '-1px 0px 0px 0px',
        padding: '0px 0px 0px 0px',
        border: '0px 0px 0px 0px',
        height: "100vh",
        transform: "translate(0%, 0%)",
    };  

    let  styleImage = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 100%)",
        zIndex: "-100"
    };
    let  styleImageHidden = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 100%)",
        visibility: "hidden",
        zIndex: "-200"
    };

    let styleImageShow = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 100%)",
        zIndex: "0"
    }; 

    if(ancho <= 600) {
        
        style = {
            top: 0,
            margin: '-1px 0px 0px 0px',
            padding: '0px 0px 0px 0px',
            border: '0px 0px 0px 0px',
            height: "100vh",
            width: "100vw", 
            transform: "translate(0%, 0%)",
        };  

        styleImage = {
            top: 0,
            padding: 0,
            border: 0,
            height: "100vh",
            width: "100vw", 
            position: "absolute",
            margin: "auto",
            transform: "translate(-50%, 100%)",
            zIndex: "-100"
        };

        styleImageHidden = {
            top: 0,
            padding: 0,
            border: 0,
            height: "100vh",
            width: "100vw", 
            position: "absolute",
            margin: "auto",
            transform: "translate(-50%, 100%)",
            visibility: "hidden",
            zIndex: "-200"
        };
    
        styleImageShow = {
            top: 0,
            padding: 0,
            border: 0,
            height: "100vh",
            width: "100%", 
            position: "absolute",
            margin: "auto",
            transform: "translate(-50%, 100%)",
            zIndex: "0"
        };  
    }

    return (
        <Fragment>
            {/* <Lottie 
                loop
                autoplay
                animationData={animationData}
                style={style}
            /> */}

            <img id="fonddoSeccion2_2" src="./imgFondos/Iglesia.gif" style={animacion ? styleImageShow : styleImageHidden} alt="foto"/>
            
            { animacion ? "" : <img id="fonddoSeccion2_1" src="./imgFondosSinAnimar/2.jpg" style={styleImage}></img> } 

            <object id="textoIglesia" type="image/svg+xml" data="./img/Iglesia.svg"></object>

            <div id="seccion2Box" ref={refSeccion2}></div>
        </Fragment>
    );
}
 
export default Seccion_2